$accent: #000;

// footer bottom BEGIN
.main {
  display: flex !important;
  flex-direction: column;
  min-height: 100vh;
  min-height: -webkit-fill-available;
  font-weight: normal;
  background: #fff;
}

img {
  max-width: 100%;
}

button:focus {
  outline: none !important;
}

.MuiInput-underline:hover:not(.Mui-disabled):before {
  border-bottom: 2px solid $accent !important;
}

nav a.active {
  background-color: rgba($accent, 0.3) !important;
}

footer {
  border-top: 1px solid rgba($accent, 0.3);
  padding: 30px 0;
  margin-top: auto;
  color: $accent;
  text-align: center;
}

.copyright {
  font-weight: bold;
}
// footer bottom BEGIN

// LOGO begin
.logo {
  display: flex;
  align-items: center;
  color: rgba($accent, 0.7);
  text-decoration: none;
  transition: all 0.3s ease-in-out;
  &:hover {
    color: rgba($accent, 1);
    .logo__img {
      opacity: 1;
    }
  }
  &__img {
    width: 100px;
    margin-right: 10px;
    opacity: 0.8;
    transition: opacity 0.3s ease-in-out;
  }
  &__text {
    display: inline-block;
    vertical-align: top;
  }
  &__title {
    font-weight: 800;
    font-size: 25px;
    line-height: 1;
    display: block;
  }
}

.nav {
  display: inline-flex;
}

.searchIcon,
.MuiButton-label {
  color: $accent;
}

.MuiCardActionArea-root {
  transition: all 0.3s ease-in-out;
}

.searchBox {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: rgba($accent, 0.2);
  border-radius: 15px;
  padding: 0 15px;
  .searchIcon {
    margin-right: 10px;
  }
  .MuiInputBase-input {
    width: 70px;
    transition: all 0.3s ease-in-out;
    &:focus {
      width: 140px;
    }
  }
  .MuiSvgIcon-root {
    vertical-align: bottom;
  }
}

.error {
  color: #f48fb1;
}

.content {
  // padding: 40px 0;
}

.MuiPagination-root {
  text-align: center;
  margin: 30px auto 30px;
  color: #fff;
  .MuiPagination-ul {
    justify-content: center;
  }
}

// Loader BEGIN
/* KEYFRAMES */

@keyframes pulse {
  from {
    opacity: 1;
    transform: scale(1);
  }
  to {
    opacity: 0.25;
    transform: scale(0.75);
  }
}

/* PULSE BUBBLES */

.pulse-container {
  margin: 30px auto 30px;
  width: 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.pulse-bubble {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background-color: $accent;
}

.pulse-bubble-1 {
  animation: pulse 0.4s ease 0s infinite alternate;
}
.pulse-bubble-2 {
  animation: pulse 0.4s ease 0.2s infinite alternate;
}
.pulse-bubble-3 {
  animation: pulse 0.4s ease 0.4s infinite alternate;
}

// Loader END

.error {
  text-align: center;
  margin: 30px 0;
  font-size: 14px;
}

.date {
  color: gray;
}

.MuiCard-root {
  max-height: 435px;
  overflow-y: scroll !important;
}

html,
body {
  height: 100%;
}

#root {
  display: flex;
  height: 100%;
  flex-direction: column;
}

.content {
  flex: 1 0 auto;
}

// loader begin
.loader,
.loader:before,
.loader:after {
  border-radius: 50%;
  width: 2.5em;
  height: 2.5em;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  -webkit-animation: load7 1.8s infinite ease-in-out;
  animation: load7 1.8s infinite ease-in-out;
}
.loader {
  color: $accent;
  font-size: 10px;
  margin: 80px auto;
  position: relative;
  text-indent: -9999em;
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-animation-delay: -0.16s;
  animation-delay: -0.16s;
}
.loader:before,
.loader:after {
  content: '';
  position: absolute;
  top: 0;
}
.loader:before {
  left: -3.5em;
  -webkit-animation-delay: -0.32s;
  animation-delay: -0.32s;
}
.loader:after {
  left: 3.5em;
}
@-webkit-keyframes load7 {
  0%,
  80%,
  100% {
    box-shadow: 0 2.5em 0 -1.3em;
  }
  40% {
    box-shadow: 0 2.5em 0 0;
  }
}
@keyframes load7 {
  0%,
  80%,
  100% {
    box-shadow: 0 2.5em 0 -1.3em;
  }
  40% {
    box-shadow: 0 2.5em 0 0;
  }
}

.example-eneter {
  opacity: 0.01;
}

.example-enter.example-enter-active {
  opacity: 1;
  transition: opacity 300ms ease-in;
}

.example-leave {
  opacity: 1;
}

.example-leave.example-leave-active {
  opacity: 0.01;
  transition: opacity 300ms ease-in;
}
//Loader end

footer {
  flex: 0 0 auto;
  padding: 10px 20px;
  display: flex;
  align-items: center;
}

// footer-logo
.footer-logo {
  flex: 0 1 auto;
  &__img {
    width: 80px;
  }
}

.copyright {
  margin: auto;
}

// Styles for tic-toc
.square {
  font: bold 24px/54px 'Jura', sans-serif;
  width: 100px;
  height: 100px;
  background: #fff;
  border: none;
  border-radius: 10px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
  &--winner {
    background: #e0e0e0;
    svg * {
      stroke: #ed4b82;
    }
  }
}

.square:hover,
.square:focus {
  background: #e0e0e0;
  outline: none;
}

.board {
  width: auto;
  height: auto;
  display: grid;
  grid-template-columns: auto auto auto;
  gap: 6px;
  border-radius: 10px;
  transition: all 0.6s ease-in-out;
  &--disabled {
    animation: hide 0.4s 3s ease-in-out forwards;
  }
}

@keyframes hide {
  100% {
    opacity: 0;
  }
}
.board--disabled button.square {
  // pointer-events: none;
  pointer-events: none !important;
  &:hover {
    background: none !important;
  }
}

.game {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
  text-align: center;
  width: 100%;
  height: calc(100vh - 140px);
  flex-direction: row;
  &--board {
    position: relative;
    margin-top: 60px;
  }
  &--title {
    position: absolute;
    left: 0;
    right: 0;
    top: -120px;
  }
}

.play-again {
  position: absolute !important;
  left: 50%;
  transform: translateX(-50%);
  bottom: -55px;
}

.game-info {
  margin-left: 20px;
}

.status {
  position: absolute;
  left: 0;
  right: 0;
  top: -40px;
  font-size: 20px;
  font-weight: bold;
  color: #009688;
  margin-bottom: 10px;

  &--nextX {
    color: #606060;
  }
  &--win {
    animation: win 0.4s 3s ease-in-out forwards;
    color: #ed4b82;
  }
}

@keyframes win {
  100% {
    top: 30%;
    font-size: 50px;
    z-index: 1;
  }
}

/* ------------------------------ ADDITIONAL STYLES ----------------------------- */
/* ------------------------------ 0 ANIMATION ----------------------------- */
.circle,
.first,
.second {
  opacity: 0;
  transition: all 0.3s ease-in-out;
}

.circle {
  display: block;
  width: 50px;
  height: 50px;
  stroke-dasharray: 283;
  stroke-dashoffset: 283;
  animation: draw 1s ease-in-out forwards;
  margin: auto;
}

/* ------------------------------ X ANIMATION ----------------------------- */
.cross {
  display: block;
  width: 45px;
  height: 45px;
  margin: auto;
}

.first {
  stroke-dasharray: 125;
  stroke-dashoffset: 125;
  -webkit-animation: draw 0.6s forwards;
  animation: draw 0.6s forwards;
}

.second {
  stroke-dasharray: 125;
  stroke-dashoffset: 125;
  -webkit-animation: draw 0.6s 0.6s forwards;
  animation: draw 0.6s 0.6s forwards;
}

/* ------------------------------ X ANIMATION ----------------------------- */
@keyframes draw {
  100% {
    stroke-dashoffset: 0;
    opacity: 1;
  }
}

/* ------------------------------ FULLSCREEN ----------------------------- */
.game:fullscreen {
  background: #eeeeee;
}

.game:-ms-fullscreen,
.game:-moz-full-screen,
.game:-webkit-full-screen {
  background: #eeeeee;
}
/* ------------------------------ FULLSCREEN ----------------------------- */

@import './media';
